.NavigationItem{
    margin: 10px 0;
    /* padding: 10px 0; */
    box-sizing: border-box;
    display: block;
    width: 100%;
    text-align: left;
    
}
.NavigationItem a{
    color: #8F5C2C;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
    /* text-align: left; */
}
.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    /* background-color: #8F5C2C; */
    color:#40A4C8;
}

@media(min-width:500px){
    .NavigationItem{
        margin: 0;
        display: flex;
        height: 100%;
        width: auto;
        align-items: center;
    }
    .NavigationItem a{
        color: white;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
       
    }
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active{
        background-color: #8f5C2C;
        border-bottom: 4px solid aqua;
        color:white;
    }
}

